* {
  font-family: 'IBM Plex Sans';
  /* font-family: 'Open Sans', sans-serif; */
  background-color: #f6f6f6ed;
}

.page__catalog {
  margin-top: 32px;
}

.catalog {
  display: grid;
  grid-template-columns: repeat(auto-fill, 324px);
  column-gap: 24px;
  row-gap: 48px;
  justify-content: center;
}

.scale {
  display: inline-block; 
  /* overflow: hidden;  */
}

 .scale img {
  transition: 0.5s; 
  display: block; 
}

 .scale img:hover {
    transform: scale(1.05); 
}   

.catalog__item {
  width: 324px;
}

.card__image {
  /* border-radius: 2%; */
  border: solid;
  border-width: 3px;
  border-color: rgb(0, 0, 255);
  cursor: pointer;
  width: 100%;
  height: 216px;
  object-fit: cover;
  object-position: center;
  border: 1px solid rgba(114, 113, 113, 0.62);
  border-radius: 4px;
  box-shadow: 5px 5px 30px rgb(0 0 0 / 50%);

}

.card__image:hover {
  /* opacity: .9; */
  /* background-color: #2546bc; */
  /* border-bottom-width: 3px; */
  /* border-bottom-color: crimson; */
  box-shadow: 5px 5px 30px rgb(243 243 129/ 70%);
}

.card__description {
  padding: 12px 0px;
  box-sizing: border-box;
}

.card__title {
  margin: 0;
  font-weight: bold;
  font-size: 18px;
  line-height: 20px;
}

.card__url {
  /* font-size: 14px;
  color: #0C0C0D;
  margin: 8px 0 0;
  opacity: 0.4; */
  /* display: block;
  color: #94949E;
  font-size: 14px;
  line-height: 1.4; */
  display: block;
  color: #3C39EC;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  text-decoration: none;
  margin-top: 8px;
  
}

.card__info {
  font-size: 14px;
  color: #0C0C0D;
  margin: 8px 0 0;
  opacity: 0.4;
  
}

.card__info{display: inline;}

.bottom {
  position:relative;
  bottom: -3px; 
  left: 0px;
}


.header {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 96px;
}

.header__logo {
  margin-right: 8px;
  width: 32px;
}

.header__title {
  font-size: 20px;
  line-height: 1.6;
  margin: 0;
  text-shadow: 4px -2px 3px #11111145;
}

.page {
	margin: 0;
	box-sizing: border-box;
	padding: 0 24px;
}

.card__description a {
	word-wrap: break-word;
}

@media (max-width: 479px) {

	.scale {
		/* width: 100%; */
	}

	.catalog__item {
		width: 100%;
	}

	.card__image {
		width: 100%;
		height: auto;
	}

	.scale img {
		margin: 0 auto;
	}

	.catalog {
		display: block;
	}

	.header__title {
		font-size: 1em;
		word-wrap: break-word;
	}

	.card__description h3 {
		font-size: 14px;
	}

	.card__description a {
		line-height: 1.2em;
		font-size: 12px;
	}
}